
.player[data-v-656aeee4]{
	width: 100%;
}
.progress[data-v-656aeee4]{
	width: 80%;
	margin-left: auto;
	margin-right: auto;
}
.progress-bar[data-v-656aeee4]{
	position: relative;
	width: 100%;
	height: .15rem;
	margin-bottom: 10px;
	background: #eee;
}
.progress-bar .progress-loaded[data-v-656aeee4]{
	width: 0;
	height: inherit;
	background: #ccc;
	-webkit-transition: all .5s ease;
	transition: all .5s ease;
}
.progress-bar .progress-now[data-v-656aeee4]{
	position: absolute;
	top: 0;
	height: inherit;
	background: #f00;
}
.progress-now .progress-point[data-v-656aeee4]{
	position: absolute;
	right: -0.18rem;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	width: .3rem;
	height: .3rem;
	background: #000;
	border-radius: 50%;
}
.progress span[data-v-656aeee4]{
	font-size: 16px;
}
span.current-time[data-v-656aeee4]{
	float: left;
}
span.total-time[data-v-656aeee4]{
	float: right;
}
.btns[data-v-656aeee4]{
	padding-top: 5px;
}
.btns i[data-v-656aeee4]{
	font-size: .8rem;
}
.btns i[data-v-656aeee4]:nth-child(2){
	margin-left: 14%;
	margin-right: 14%;
}



